<template>
    <div class="card">
        <h3 class="card-title"> <b>{{ nombre }}</b> </h3>

        <clientecobro
            v-for="c, index in clientes" :key="index"
            :cliente = "c"
        />
        
    </div>
</template>

<script>
import { defineAsyncComponent } from 'vue'
export default {
    props:{
        nombre:{
            type: String
        },
        clientes:{
            type: Array
        }
    },
    components:{
        clientecobro : defineAsyncComponent (()=> import('@/modules/rifa/components/cobro/clientecobro.vue'))
    }
}
</script>

<style scoped>

    .card{
        text-align: center;
    }

    h3{
        text-transform: uppercase;
    }

</style>